import getGifs from './getGifs';
import axios from 'axios';

function InputArea() {
  // 最多尝试加载
  this.container = document.getElementById('inputList');
  this.timeLimit = 20000;
  this.init();
}
/**
 * 图片预览
 */
InputArea.prototype.onFileChange = function (domFile) {
  if (!domFile && !domFile.files) {
    return;
  }
  const domImg = domFile.nextElementSibling;
  const reader = new FileReader();
  const file = domFile.files[0];
  //新建 FileReader 对象
  reader.onload = function () {
    // 当 FileReader 读取文件时候，读取的结果会放在 FileReader.result 属性中
    domImg.src = this.result;
  };
  // 设置以什么方式读取文件，这里以base64方式
  reader.readAsDataURL(file);
};

/**
 * 获取表单信息
 *
 * @param {*} from
 * @returns
 */
InputArea.prototype.getFormData = function (from) {
  const { timeLimit } = this;
  // 图片加载
  const promiseImg = new Promise((resolve, reject) => {
    const domImg = from.querySelector('._img img');
    const imgW = domImg.naturalWidth;
    const imgH = domImg.naturalHeight;
    const isGif = domImg.src.substr(0, 14) === 'data:image/gif';
    if (isGif) {
      const src = domImg.src;
      getGifs(src, imgW, imgH).then(({ duration, images }) => {
        resolve({
          durationGif: parseInt(duration),
          imgSrc: images,
          imgW,
          imgH,
        });
      });
    } else {
      resolve({
        imgSrc: [domImg.src],
        imgW,
        imgH,
      });
    }
    // 加载超时
    setTimeout(() => {
      reject();
    }, timeLimit);
  });

  // 音频加载
  const promiseAudio = new Promise((resolve, reject) => {
    const audio = from.querySelector('._audio audio');
    audio.load();
    audio.oncanplaythrough = function () {
      resolve({
        domAudio: audio,
        duration: parseInt(audio.dataset.duration),
      });
    };
    // 加载超时
    setTimeout(() => {
      reject();
    }, timeLimit);
  });

  return new Promise((resolve, reject) => {
    const text = from.text.value;
    Promise.allSettled([promiseImg, promiseAudio]).then(
      ([resultImg, resultAudio]) => {
        const isSuccess =
          resultImg.status === 'fulfilled' &&
          resultAudio.status === 'fulfilled';
        // console.log(resultImg, resultAudio);
        if (isSuccess) {
          const d = Object.assign({ text }, resultImg.value, resultAudio.value);
          resolve(d);
        } else {
          reject();
        }
      },
    );
  });
};
/**
 * 获取数据
 */
InputArea.prototype.getData = function () {
  const container = this.container;
  const forms = container.querySelectorAll('form');
  const promises = [];
  forms.forEach((from, key) => {
    key < forms.length - 1 && promises.push(this.getFormData(from));
  });
  return new Promise((resolve) => {
    Promise.allSettled(promises).then((result) => {
      const data = [];
      result.forEach(({ status, value }) => {
        status === 'fulfilled' && data.push(value);
      });
      resolve(data);
    });
  });
};

/**
 * 找到最近的 form 元素
 * @param {*} target
 * @returns
 */
InputArea.prototype.getParentsForm = function (target) {
  let form = false;
  let nodeParent = target.parentNode;
  while (
    !(
      nodeParent.tagName.toLowerCase() === 'form' ||
      nodeParent.tagName.toLowerCase() === 'body'
    )
  ) {
    nodeParent = nodeParent.parentNode;
  }
  if (nodeParent.tagName.toLowerCase() === 'form') {
    form = nodeParent;
  }
  return form;
};

/**
 * 添加新帧
 * @param {*} domForm
 */
InputArea.prototype.getNewItemByData = function ({
  text,
  srcImg,
  srcAudio,
  duration,
}) {
  let strTpl = document.getElementById('tplInput').innerHTML.trim();
  strTpl = strTpl.replace(new RegExp('//srcImg//', 'gm'), srcImg);
  strTpl = strTpl.replace(new RegExp('//text//', 'gm'), text);
  strTpl = strTpl.replace(new RegExp('//srcAudio//', 'gm'), srcAudio);
  strTpl = strTpl.replace(new RegExp('//duration//', 'gm'), duration);
  const objE = document.createElement('div');
  objE.innerHTML = strTpl;
  return objE.childNodes[0];
};

/**
 * 更新表单声音
 * @param form
 * @returns {boolean}
 */
InputArea.prototype.updateAudioByForm = function (form) {
  const { text, speed, voice } = form;
  const audio = form.querySelector('._audio audio');
  audio.removeAttribute('src');
  if (!text.value) {
    return false;
  }
  let src =
    'https://service-5bcyoaw2-1251191376.gz.apigw.tencentcs.com/release/tts?';
  src += 'text=' + text.value + '!!!!!';
  src += '&speed=' + speed.value;
  src += '&voice=' + voice.value;
  form.classList.add('_loading');
  axios.get(src).then(({ data = {} }) => {
    form.classList.remove('_loading');
    if (data.duration > 0) {
      audio.src = 'data:audio/mp3;base64,' + data.audio;
      audio.dataset.duration = data.duration * 1000;
      audio.load();
    }
  });
};

/**
 * 添加表单
 */
InputArea.prototype.initAdd = function () {
  const _it = this;
  const form = document.getElementById('addForm');
  const submit = document.getElementById('addBtn');
  const audio = form.querySelector('audio');
  const img = form.querySelector('._img img');

  submit.addEventListener('click', function (e) {
    // 声音还没有加载完
    if (!!form.text.value && !audio.src) {
      e.preventDefault();
      submit.classList.add('_loading');
      // 声音加载完再提交
      audio.oncanplaythrough = function () {
        submit.classList.remove('_loading');
        submit.click();
      };
    }
  });

  // form.img.addEventListener('invalid', function () {
  //   console.log('img', this.validity.valueMissing);
  //   if (this.validity.valueMissing) {
  //     this.setCustomValidity('再见');
  //   } else {
  //     this.setCustomValidity('');
  //   }
  // });
  // form.text.addEventListener('invalid', function () {
  //   console.log('text', this.validity.valueMissing);
  //   if (this.validity.valueMissing) {
  //     this.setCustomValidity('再见2');
  //   } else {
  //     this.setCustomValidity('');
  //   }
  // });

  // 表单提交
  form.addEventListener('submit', function (e) {
    e.preventDefault();
    const itemNew = _it.getNewItemByData({
      text: form.text.value,
      srcImg: img.src,
      srcAudio: audio.src,
      duration: audio.dataset.duration,
    });
    form.parentNode.insertBefore(itemNew, form);
    form.reset();
    img.removeAttribute('src');
    audio.removeAttribute('src');
    form.scrollIntoView();
  });
};
/**
 * 根据文字修改声音
 */
InputArea.prototype.init = function () {
  const _it = this;
  const container = this.container;

  // 修改声音或者图片
  container.addEventListener('change', function ({ target }) {
    if (target) {
      // 动态修改图片
      target.name === 'img' && _it.onFileChange(target);
      // 更新声音
      const arrVoice = ['text', 'speed', 'voice'];
      if (arrVoice.includes(target.name)) {
        const form = _it.getParentsForm(target);
        _it.updateAudioByForm(form);
      }
    }
  });

  // 删除
  container.addEventListener('click', function ({ target }) {
    if (target && target.dataset.status === 'del') {
      const form = _it.getParentsForm(target);
      form.parentNode.removeChild(form);
    }
  });

  this.initAdd();
};

export default InputArea;
