import SuperGif from "./libgif.js";

function _base64ToArrayBuffer(base64) {
    var binary_string = window.atob(base64);
    var len = binary_string.length;
    var bytes = new Uint8Array(len);
    for (var i = 0; i < len; i++) {
        bytes[i] = binary_string.charCodeAt(i);
    }
    return bytes.buffer;
}

function getGifs(gifBase64, width, height) {
    return new Promise((resolve) => {
        const img = document.createElement('img');
        img.src = gifBase64;
        img.width = width;
        img.height = height;
        document.getElementById('imgLoader').appendChild(img);
        const totalFramesInfo = [];
        const rub = new SuperGif({ gif: img });
        rub.load(function () {
            // 获取所有的 gif 图片
            const totalFrames = rub.get_length();
            const crtCanvas = rub.get_canvas();
            for (let i = 0; i < totalFrames; i++) {
                rub.move_to(i);
                totalFramesInfo.push(crtCanvas.toDataURL());
            };

            // 获取每一桢的时间
            const ab = _base64ToArrayBuffer(gifBase64.split(',')[1]);
            const u8a = new Uint8Array(ab);
            let duration = 0;
            for ( let i = 0; i < u8a.length; i++) {
                // Find a Graphic Control Extension hex(21F904__ ____ __00)
                if (u8a[i] == 0x21
                    && u8a[i + 1] == 0xF9
                    && u8a[i + 2] == 0x04
                    && u8a[i + 7] == 0x00) {
                    // Swap 5th and 6th bytes to get the delay per frame
                    const delay = (u8a[i + 5] << 8) | (u8a[i + 4] & 0xFF)
                    duration += delay < 2 ? 10 : delay;
                }
            }
            resolve({
                duration: duration * 10,
                images: totalFramesInfo,
            });
        });
    });
}
export default getGifs;