import { Scene, Label, Sprite, Rect } from 'spritejs';
import _sleep from '../utils/sleep';

function AniStage({
  // 每一帧间隔时间
  timeGap = 0,
  // 最后黑屏时间
  timeEndMore = 500,
  container,
} = {}) {
  this.timeGap = timeGap;
  this.container = container;
  this.timeEndMore = timeEndMore;
  this.init();
}
/**
 * 初始化
 */
AniStage.prototype.init = function () {
  const { container } = this;
  const width = container.clientWidth;
  const height = container.clientHeight;
  const fillColor = window.getComputedStyle(container).backgroundColor;
  this.width = width;
  this.height = height;
  const scene = new Scene({
    container,
    width,
    height,
  });
  const layer = scene.layer();
  this.canvas = layer.canvas;

  // 需要填充一个背景色避免录屏工具录不出来
  const rectBg = new Rect({
    width,
    height,
    fillColor,
  });
  layer.append(rectBg);
  this.layer = layer;
};
/**
 *
 */
AniStage.prototype.getCutSize = function ([w1, h1], [w2, h2]) {
  // 如果两个宽度都小于总宽度那么按照原始尺寸返回
  const [w, h] =
    w1 / h1 < w2 / h2
      ? [parseInt((h2 / h1) * w1), h2]
      : [w2, parseInt((w2 / w1) * h1)];
  return [w, h];
};
/**
 * 显示某一帧
 */
AniStage.prototype.showFrame = function (
  { imgSrc, imgW, imgH, text, duration, durationGif, domAudio },
  isLast,
) {
  return new Promise((resolve) => {
    const { width, height, layer, timeEndMore } = this;
    const size = this.getCutSize([imgW, imgH], [width * 0.95, height * 0.8]);
    domAudio.currentTime = 0;
    domAudio.play();
    // 组装图片数组
    const textures = imgSrc.map((item) => ({
      texture: item,
    }));

    // 创建图片对象
    const spriteImg = new Sprite({
      anchor: [0.5, 0.5],
      size,
      // 720*0.85*0.5
      pos: [width / 2, height / 2 - 720 * 0.2 * 0.3],
      texture: imgSrc[0],
    });
    layer.append(spriteImg);
    if (durationGif > 0) {
      spriteImg.animate(textures, {
        duration: durationGif,
        fill: 'forwards',
        iterations: Infinity,
      });
    }
    // 分解文字
    const arrText = (function () {
      const textArray = [];
      text.split('').forEach((item, key) => {
        !!item &&
          textArray.push({
            text: ' ' + text.substr(0, key + 1).toString() + ' ',
          });
      });
      return textArray;
    })();
    // 创建文字对象
    const label = new Label({
      anchor: [0.5, 0.5],
      pos: [width / 2, height - 60],
      fillColor: '#ffffff',
      padding: [10, 0],
      font: 'bold 40px sans-serif',
    });
    layer.append(label);
    label.animate(arrText, {
      duration: duration,
      fill: 'forwards',
    });
    if (!isLast) {
      setTimeout(() => {
        spriteImg.remove();
        label.remove();
        resolve();
      }, duration);
    } else {
      // console.log({ isLast });
      // 这里是为了解决视频结尾被截断的问题
      setTimeout(() => {
        label.animate(
          [
            {
              scale: 1,
            },
          ],
          {
            duration: timeEndMore,
            fill: 'forwards',
          },
        );
        setTimeout(() => {
          spriteImg.remove();
          label.remove();
          resolve();
        }, timeEndMore);
      }, duration);
    }
  });
};

/**
 * 开始播放
 */
AniStage.prototype.play = function (data = []) {
  return new Promise((resolve) => {
    if (!data.length) {
      resolve('您还没有输入内容');
      return;
    }
    const _it = this;
    const widthWin = document.body.clientWidth;
    const numScale = widthWin < _it.width ? widthWin / _it.width : 1;
    _it.container.style.transform = 'scale(' + numScale + ')';
    async function showFrames() {
      for (let i = 0; i < data.length; i++) {
        await _it.showFrame(data[i], i === data.length - 1);
      }
      resolve();
    }
    showFrames();
  });
};
export default AniStage;
