// CanvasRecorder.js - smusamashah
// To record canvas effitiently using MediaRecorder
// https://webrtc.github.io/samples/src/content/capture/canvas-record/

function Recorder({ canvas, options = {} }) {
  if (typeof MediaRecorder !== 'function') {
    return '您的浏览器不支持 MediaRecorder, 建议使用 chrome 或者升级！';
  }

  // 视频格式记录的类型
  const supportedType = this.getSupportedType();

  if (supportedType == null) {
    return '未找到您设备支持的视频格式, 建议使用 chrome 或者升级！';
  }
  this.supportedType = supportedType;
  const stream = canvas.captureStream();
  if (typeof stream == undefined || !stream) {
    return '您的浏览器不支持 canvas.captureStream, 建议使用 chrome 或者升级！';
  }
  const mediaRecorder = new MediaRecorder(stream, this.options);
  this.stream = stream;
  // 录制下来的文件
  this.recordedBlobs = [];
  mediaRecorder.ondataavailable = ({ data }) => {
    data && data.size > 0 && this.recordedBlobs.push(data);
  };

  this.mediaRecorder = mediaRecorder;
  this.audioCtx = new (window.AudioContext || window.webkitAudioContext)();
  this.audioDest = this.audioCtx.createMediaStreamDestination();
  // error
  this.blob = null;

  // 配置参数
  this.options = Object.assign(
    {
      mimeType: supportedType,
      videoBitsPerSecond: 2500000, // 2.5Mbps
    },
    options,
  );
}

/**
 * 开始录制
 *
 * @returns
 */
Recorder.prototype.start = function () {
  this.recordedBlobs = [];
  this.mediaRecorder && this.mediaRecorder.start(100);
};

Recorder.prototype.addAudiosToStream = function (audios = []) {
  if (!audios.length) {
    return;
  }
  try{
    var audioCtx = this.audioCtx;
    var dest = this.audioDest;
    var audioStream = dest.stream;
    audios.forEach((audio) => {
      if (!audio.isConnect) {
        // createMediaElementSource 不能被同时设定到一个对象上面
        var sourceNode = audioCtx.createMediaElementSource(audio);
        // 这个是管录制的
        sourceNode.connect(dest);
        // 这个是管播放的
        sourceNode.connect(audioCtx.destination);
        audio.isConnect = true;
      }
    });
    const track = audioStream.getAudioTracks()[0];
    this.stream.addTrack(track);
  } catch (e) {

  }
};

/**
 * 停止
 *
 */
Recorder.prototype.stop = function () {
  this.mediaRecorder && this.mediaRecorder.stop();
  if(this.recordedBlobs.length){
    this.blob = new Blob(this.recordedBlobs, { type: this.supportedType });
  }
};
// 获取支持的格式
Recorder.prototype.getSupportedType = function () {
  let types = [
    'video/vp8',
    'video/webm;codecs=vp8',
    'video/webm',
    'video/webm,codecs=vp9',
    'video/webm;codecs=daala',
    'video/webm;codecs=h264',
    'video/mpeg',
  ];
  for (let i in types) {
    if (MediaRecorder.isTypeSupported(types[i])) {
      return types[i];
    }
  }
  return false;
};

/**
 * 保存文件
 *
 * @param {*} file_name
 */
Recorder.prototype.save = function (file_name) {
  const url = window.URL.createObjectURL(this.blob);
  const name = file_name + '.webm';
  const a = document.createElement('a');
  a.style.display = 'none';
  a.href = url;
  a.download = name;
  document.body.appendChild(a);
  a.click();
  setTimeout(() => {
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
  }, 100);
};

export default Recorder;
