import axios from 'axios';
import AniStage from '../components/AniStage';
import InputArea from '../components/InputArea';
import Recorder from '../components/Recorder';
import _utils from '../utils/index';
function App() {
  this.pageInput = document.getElementById('pageInput');
  this.pageDownload = document.getElementById('pageDownload');
  this.preVideo = document.getElementById('preVideo');
  this.btnStart = document.getElementById('btnStart');
  this.domMask = document.getElementById('mask');
  this.recorder = null;
  this.init();
}

App.prototype.loading = function () {
  this.pageInput.classList.add('_hide');
  this.domMask.classList.add('_show');
};

App.prototype.unLoading = function () {
  this.domMask.classList.remove('_show');
};

App.prototype.showPageInput = function () {
  this.pageInput.classList.remove('_hide');
  this.pageDownload.classList.add('_hide');
  this.pageDownload.classList.remove('_save');
  this.preVideo.pause();
};

App.prototype.showPageDownload = function () {
  if (this.recorder && this.recorder.blob) {
    this.preVideo.src = window.URL.createObjectURL(this.recorder.blob);
    this.preVideo.currentTime = 0;
    this.preVideo.play();
  }
  this.pageInput.classList.add('_hide');
  this.pageDownload.classList.remove('_hide');
};

/**
 *
 * [获取用户输入的数据]
 * @param {*} callback
 * @returns
 */
App.prototype.getInputData = function () {
  // 至少加载一秒
  return new Promise((resolve, reject) => {
    const _it = this;
    this.loading();
    Promise.all([_it.apiInputArea.getData(), _utils.sleep(1000)]).then(
      ([data]) => {
        _it.unLoading();
        if (data.length) {
          resolve(data);
        } else {
          _it.showPageInput();
          alert('你输入的内容有误，请重试!');
          reject();
        }
      },
    );
  });
};

/**
 *
 * [获取动画API]
 * @returns
 */
App.prototype.getApiAniStage = function () {
  let apiAniStage = this.apiAniStage;
  if (apiAniStage) {
    return apiAniStage;
  }
  apiAniStage = new AniStage({
    container: document.getElementById('stage'),
  });
  this.apiAniStage = apiAniStage;
  return apiAniStage;
};

App.prototype.saveBlob = function () {
  return new Promise((resolve) => {
    if (!(this.recorder && this.recorder.blob)) {
      reject();
      return;
    }
    const reader = new FileReader();
    reader.readAsDataURL(this.recorder.blob);
    reader.onload = function () {
      const result = this.result.split(',')[1];
      axios
        .post(
          'https://service-ex4kwzeu-1251191376.gz.apigw.tencentcs.com/release/webm2mp4',
          {
            video: result,
          },
        )
        .then(({ data }) => {
          if (!data.mp4) {
            reject();
            return;
          }
          const fileSrc = data.mp4;
          const newUrl =
            'https://service-i0uu6ysg-1251191376.gz.apigw.tencentcs.com/release/fileExist?file=' +
            encodeURIComponent(fileSrc);
          let counter = 0;
          const timer = setInterval(() => {
            console.log(counter);
            axios.get(newUrl).then(() => {
              resolve(fileSrc);
              clearInterval(timer);
            });
            counter++;
            // 最多请求28次
            if (counter > 20) {
              clearInterval(timer);
              reject();
            }
          }, 5000);
        });
    };
  });
};

App.prototype.getRecorder = function (data) {
  if (this.recorder) {
    return this.recorder;
  }
  this.recorder = new Recorder(data);
  return this.recorder;
};

/**
 * [初始化]
 */
App.prototype.init = function () {
  var _it = this;
  this.apiInputArea = new InputArea();

  // 正式拍摄
  const btnPlay = document.getElementById('btnPlay');
  // 回退
  const btnReEdit = document.getElementById('btnReEdit');
  const btSave = document.getElementById('btnSave');
  // 让最后一帧划入页面
  document.getElementById('inputItemPlaceholder').scrollIntoView();
  let canSaveFile = true;

  // 重新编辑
  btnReEdit.addEventListener('click', function (e) {
    e.preventDefault();
    _it.showPageInput();
    canSaveFile = false;
    btSave.classList.remove('_loading');
  });

  btnPlay.addEventListener('click', function (e) {
    e.preventDefault();
    this.classList.add('_loading');
    canSaveFile = true;
    _it.saveBlob().then((src) => {
      if (canSaveFile) {
        this.classList.remove('_loading');
        btSave.href = src;
        _it.pageDownload.classList.add('_save');
      }
    });
  });

  const btnStart = this.btnStart;
  // 点击按钮
  btnStart.addEventListener('click', function (e) {
    e.preventDefault();
    _it.getInputData().then((data) => {
      const apiAniStage = _it.getApiAniStage();
      const recorder = _it.getRecorder({
        canvas: apiAniStage.canvas,
      });
      if (recorder) {
        const audios = data.map(({ domAudio }) => domAudio);
        recorder.addAudiosToStream(audios);
        recorder.start();
      }
      apiAniStage.play(data).then(() => {
        console.log('End');
        if (recorder && recorder.recordedBlobs.length) {
          recorder.stop();
          _it.showPageDownload();
        } else {
          _it.showPageInput();
        }
      });
    });
  });
  btnStart.disabled = false;
};

window.onload = function () {
  new App();
};
